import "../styles.css";
import React from "react";
import { graphql } from "gatsby";
import MasterTemplate from "../components/MasterTemplate";
import PostLink from "../components/PostLink";
import SEO from "../components/seo";
// styles
import PostsContainer from "../styles/PostsContainer";

export default function Home({
  data: {
    allMarkdownRemark: { edges },
    site: { siteMetadata },
  },
}) {
  const Posts = edges
    .filter((edge) => !!edge.node.frontmatter.isArticle)
    .filter((edge) => !!edge.node.frontmatter.date)
    .map((edge) => (
      <PostLink key={edge.node.id} post={edge.node.frontmatter} />
    ));

  return (
    <MasterTemplate>
      <SEO title={siteMetadata.title} description={siteMetadata.description} />
      <PostsContainer>{Posts}</PostsContainer>
    </MasterTemplate>
  );
}

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            isArticle
            slug
            title
            description
            tag
            audioURL
            readTime
          }
        }
      }
    }
    site(siteMetadata: {}) {
      siteMetadata {
        title
        description
      }
    }
  }
`;
